.App .About {
  text-align: left;
}

.App .About .Bookmark {
  text-align: center;
}
.App .About .Bookmark .fa-bookmark {
  font-size: 10vh;
}
.App .About .Bookmark p {
  color: white;
  margin-top: -4rem;
}
.App .About .Bookmark .Description-header {
  margin-top: 3rem;
  margin-bottom: 0;
  color: black;
}
.App .About .Bookmark .Description {
  margin-top: 0.1rem;
  color: black;
}

.App .About .Eddie {
  text-align: center;
}
.App .About .Eddie img {
  width: 50vw;
  margin: 1rem;
}

.App .About .Skills ul {
  margin: 1rem 0 1rem 0;
  list-style: none;
}
.App .About .Skills ul li {
  margin-top: -0.8rem;
}
.App .About .Skills .Language {
  color: black;
  position: relative;
  top: 1.3rem;
  margin: 0;
}
.App .About .Skills .progress {
  height: 1.5rem;
  position: relative;
  left: 20vw;
  width: 60vw;
}

@media only screen and (min-width: 796px) {
  .App .About .Eddie img {
    width: 15vw;
    margin: 1rem;
  }

  .App .About .Skills ul li {
    margin-top: 0;
  }
  .App .About .Skills .Language {
    color: black;
    position: relative;
    top: 1.3rem;
    margin: 0;
  }
  .App .About .Skills .progress {
    height: 1.5rem;
    position: relative;
    left: 10vw;
    width: 40vw;
  }
}
