.App .Contact {
  background-color: #282c34;
  color: white;
  margin-top: 3rem;
}

.App .Contact .Triangle {
  position: relative;
  top: -1px;
  width: 0;
  height: 0;
  border-left: 50vw solid transparent;
  border-right: 50vw solid transparent;
  border-top: 3rem solid white;
}

.App .Contact .Back-to-Top {
  position: fixed;
  bottom: 2rem;
}

.App .Contact .social {
  padding: 1rem 2rem 1rem 2rem;
}
@media only screen and (max-width: 798px) {
  .App .Contact .social .row .col {
    padding: 0;
  }
}
.App .Contact .social a,
.App .Contact .social a:active,
.App .Contact .social a:visited {
  color: white;
  font-size: 2rem;
  text-decoration: none;
}
.App .Contact .social .row .col:first-child a {
  color: #007bff;
}
.App .Contact .social .row .col:nth-child(2) a {
  color: #28a745;
}
.App .Contact .social .row .col:nth-child(3) a {
  color: #dc3545;
}
.App .Contact .social .row .col:nth-child(5) a {
  color: #00356d;
}
.App .Contact .social .row .col:nth-child(6) a {
  color: #014912;
}

@media only screen and (min-width: 796px) {
  .App .Contact .social {
    margin-top: 10vh;
  }
}
